

<script lang="js">
import { isElementNotInViewport,throttle } from '@scripts/utils'
export default ({
    mounted() {
        this.width = this.$refs.card.offsetWidth;
        this.height = this.$refs.card.offsetHeight;
        this.$nextTick(()=>{
            this.preloadUrl = this.dataImage
            this.$forceUpdate()
            window.addEventListener('scroll', throttle(() => {
                if(!isElementNotInViewport(this.$refs.card)){
                    this.cardClass = 'xyz-in'
                    return
                }else{
                    this.cardClass = 'xyz-out'
                }
            },100))
        })
    },
    props: ['dataImage'],
    data: () => ({
        width: 0,
        height: 0,
        mouseX: 0,
        mouseY: 0,
        mouseLeaveDelay: null,
        setImageDelay:null,
        backUrl:'',
        preloadUrl:'',
        cardClass: 'xyz-in'
    }),
    computed: {
        mousePX() {
            return this.mouseX / this.width;
        },
        mousePY() {
            return this.mouseY / this.height;
        },
        cardStyle() {
            const rX = this.mousePX * 30;
            const rY = this.mousePY * -30;
            return {
                transform: `rotateY(${rX}deg) rotateX(${rY}deg)`
            };
        },
        cardBgTransform() {
            const tX = this.mousePX * -40;
            const tY = this.mousePY * -40;
            return {
                transform: `translateX(${tX}px) translateY(${tY}px)`
            }
        },
        cardBgImage() {
            return {
                backgroundImage: `url(${this.backUrl})`
            }
        }
    },
    methods: {
        handleMouseMove(e) {
            this.mouseX = e.pageX - this.$refs?.card?.offsetLeft - this.width / 2;
            this.mouseY = e.pageY - this.$refs?.card?.offsetTop - this.height / 2;
        },
        handleMouseEnter() {
            clearTimeout(this.mouseLeaveDelay);
            this.setImageDelay = setTimeout(()=>{
                this.backUrl = this.dataImage
            },100)
        },
        handleMouseLeave() {
            clearTimeout(this.setImageDelay)
            this.mouseLeaveDelay = setTimeout(() => {
                this.mouseX = 0;
                this.mouseY = 0;
                this.backUrl = ""
            }, 5000);
        }
    }
})
</script>

<template>
    <div :class="['card-wrap',cardClass]" 
        @mousemove="handleMouseMove" 
        @mouseenter="handleMouseEnter" 
        @mouseleave="handleMouseLeave"
        ref="card">
        <div class="card" :style="cardStyle">
            <div class="card-bg" :style="[cardBgTransform, cardBgImage]"></div>
            <div class="card-info">
                <slot name="header"></slot>
                <slot name="content"></slot>
            </div>
        </div>
    </div>
    <img :src="preloadUrl"  v-show="false">
</template>

<style lang="scss" scoped>
$hoverEasing: cubic-bezier(0.23, 1, 0.32, 1);
$returnEasing: cubic-bezier(0.445, 0.05, 0.55, 0.95);

.card-wrap {
    margin: 10px;
    transform: perspective(800px);
    transform-style: preserve-3d;
    cursor: pointer;
    // background-color: #fff;

    &:hover {
        .card-info {
            transform: translateY(0);
        }

        .card-info p {
            opacity: 1;
        }

        .card-info,
        .card-info p {
            transition: 0.6s $hoverEasing;
        }

        .card-info:after {
            transition: 5s $hoverEasing;
            opacity: 1;
            transform: translateY(0);
        }

        .card-bg {
            transition:0.6s $hoverEasing,opacity 5s $hoverEasing;
            opacity: 0.8;
        }

        .card {
            transition:
                0.6s $hoverEasing,
                box-shadow 2s $hoverEasing;
            box-shadow:
                rgba(white, 0.2) 0 0 40px 5px,
                rgba(white, 1) 0 0 0 1px,
                rgba(black, 0.66) 0 30px 60px 0,
                inset #333 0 0 0 5px,
                inset white 0 0 0 6px;
        }
    }
}

.card {
    position: relative;
    flex: 0 0 240px;
    width: 18vw;
    height: calc(18vw*16/10);
    background-color: #333;
    overflow: hidden;
    border-radius: 10px;
    box-shadow:
        rgba(black, 0.66) 0 30px 60px 0,
        inset #333 0 0 0 5px,
        inset rgba(white, 0.5) 0 0 0 6px;
    transition: 1s $returnEasing;
}

.card-bg {
    opacity: 0.5;
    position: absolute;
    top: -20px;
    left: -20px;
    width: 100%;
    height: 100%;
    padding: 1vw;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: rgb(random(235),random(235),random(235));
    transition:
        all 1s $returnEasing,
        opacity 5s 1s $returnEasing,
        background-image .1s .1s $returnEasing;
    pointer-events: none;
}

.card-info {
    padding: 2vw;
    position: absolute;
    bottom: 0;
    color: #fff;
    transform: translateY(40%);
    transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);

    p {
        opacity: 0;
        text-shadow: rgba(black, 1) 0 2px 3px;
        overflow: hidden;
        transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    * {
        position: relative;
        z-index: 1;
    }

    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(to bottom, transparent 0%, rgba(#000, 0.6) 100%);
        background-blend-mode: overlay;
        opacity: 0;
        transform: translateY(100%);
        transition: 5s 1s $returnEasing;
    }
}

.card-info h1 {
    font-family: "Playfair Display";
    font-size: 2vw;
    font-weight: 700;
    text-shadow: rgba(black, 0.5) 0 10px 10px;
}
</style>



